import React from 'react';
import { UserContext } from '../contexts';

export const UiRestriction = ({ name, link, requireLogin = false, requireAdmin = false, hidden = false, children, ...rest }) => (
  <UserContext.Consumer>
    {({ userInfo }) => {
      let clonedChildren = React.Children.map(children, child => React.cloneElement(child, rest)) ;
      if (!userInfo) {
        if (requireLogin || requireAdmin) {
          return <></>;
        }
        return <>{clonedChildren}</>;
      } else {
        if (hidden || (requireAdmin && !userInfo.isAdmin)) {
          return (<></>)
        }
        return <>{clonedChildren}</>
      }
    }}
  </UserContext.Consumer>
)