import React from 'react';
import { Drawer, Form, Button, Col, Row, Input, Affix, Tooltip } from 'antd';
import axios from 'axios';
import { QuestionCircleTwoTone } from '@ant-design/icons';

import { notificationApi } from '../config'

class InquiryForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = { visible: false };
    this.formRef = React.createRef();
  }

  showDrawer = () => {
    this.setState({
      visible: true,
    });
  };

  onClose = (e) => {
    this.setState({
      visible: false,
    });
  };

  onSubmit = async data => {
    const res = await axios.post(notificationApi.add, data);
    this.formRef.current.resetFields()
    this.onClose();
  }

  render() {
    return (
      <div>
        <Affix style={{ position: 'fixed', bottom: '30px', right: '20px' }} >
          <Tooltip title="Have some Query?" placement="left">
            <QuestionCircleTwoTone style={{ fontSize: '50px' }} onClick={this.showDrawer} />
          </Tooltip>
        </Affix>
        <Drawer
          title="Send us your query"
          onClose={this.onClose}
          visible={this.state.visible}
          bodyStyle={{ paddingBottom: 80 }}
        >
          <Form layout="vertical"
            hideRequiredMark
            onFinish={this.onSubmit}
            ref={this.formRef}
          // onFinishFailed={onFinishFailed}
          >
            <Row gutter={16}>
              <Col span={24}>
                <Form.Item
                  name="name"
                  label="Name"
                  rules={[{ required: true, message: 'Please enter user name' }]}
                >
                  <Input placeholder="Please enter user name" />
                </Form.Item>
              </Col>
              <Col span={24}>
                <Form.Item
                  name="from"
                  label="Email"
                  rules={[{ required: true, type: 'email', message: 'Please enter user email' }]}
                >
                  <Input placeholder="Please enter user name" />
                </Form.Item>
              </Col>
              {/* <Col span={24}>
                <Form.Item
                  name="mobile"
                  label="Mobile No."
                  rules={[{ type: 'number', min: 10, max: 15 }]}
                >
                  <Input placeholder="Please enter user name" />
                </Form.Item>
              </Col> */}
            </Row>
            <Row gutter={16}>
              <Col span={24}>
                <Form.Item
                  name="query"
                  label="Description"
                  rules={[
                    {
                      required: true,
                      message: 'please enter your query',
                    },
                  ]}
                >
                  <Input.TextArea rows={4} placeholder="Please enter your query" />
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={24}>
                <Button
                  onClick={this.onClose}
                  htmlType='reset'
                  style={{ marginRight: 8 }}
                >
                  Cancel
              </Button>
                <Button
                  type="primary"
                  htmlType='submit'
                >
                  Submit
              </Button>
              </Col>
            </Row>
          </Form>
        </Drawer>
      </div>
    );
  }
}

export default InquiryForm;