import React, { Component } from 'react';
import axios from 'axios';
import { userApi } from '../config';

const UserContext = React.createContext({});

class UserDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userInfo: undefined
    }
  }

  componentDidMount() {
    this.validateLogin();
  }

  login = async (credentials) => {
    try {
      let { data: { token, userInfo } } = await axios.post(userApi.login, credentials);
      localStorage.setItem('user-token', token);
      this.setState({ userInfo });
    } catch (err) {
      if (err.message === 'Request failed with status code 401') {
        this.clearSession();
      }
    }
  }

  clearSession = () => {
    localStorage.removeItem('user-token');
    this.setState({ userInfo: undefined });
  }

  getUserToken = () => {
    return localStorage.getItem('user-token') || '';
  }

  validateLogin = async () => {
    try {
      if (this.getUserToken()) {
        let { data: userInfo } = await axios.get(userApi.verify, this.getAuthHeader());
        this.setState({ userInfo })
      }
    } catch (err) {
      if (err.message === 'Request failed with status code 401') {
        this.clearSession();
      }
    }
  }

  getAuthHeader = () => {
    return { headers: { Authorization: 'Bearer ' + this.getUserToken() } };
  }

  logOut = async () => {
    try {
      // await axios.post(config.url + 'logout', {}, this.getUserToken());
      this.clearSession();
    } catch (err) {
      // TODO: Think of something
    }
  }

  render() {
    const { children } = this.props;
    const { login, logOut, getAuthHeader } = this;
    const { userInfo } = this.state;
    return (
      <UserContext.Provider value={{
        login,
        logOut,
        getAuthHeader,
        userInfo
      }}>
        {children}
      </UserContext.Provider>
    )
  }
}

export { UserContext, UserDetails };