import React from 'react';
import { Layout, Menu, Avatar } from 'antd';
import { withRouter } from 'react-router-dom';
import { UiRestriction, InquiryForm } from '../components';
import { UseContext } from '../hoc';
import { UserContext } from '../contexts';

const { Header, Content, Footer } = Layout,
  { SubMenu, Item } = Menu;

const layoutConfig = [
  {
    name: 'Home',
    link: '/',
  }, {
    name: 'Alternaors',
    link: '/alternators',
  }, {
    //   name: 'Spare Parts',
    //   link: '/spare'
    // }, {
    name: 'Login',
    link: '/login',
    hidden: true
  }, {
    name: 'Profile',
    link: '/profile',
    requireLogin: true,
    subMenu: 'renderProfileMenu'
  }
]

const profileMenuConfig = [
  {
    name: 'Reset Password',
    link: '/password/reset',
  }, {
    name: 'Admin Panel',
    link: '/admin',
  }, {
    name: 'Logout',
    link: '/logout',
  },
]

class MainLayout extends React.Component {
  onMenuItemClick = ({ key }) => {
    if (key === '/logout') {
      const { logOut } = this.props;
      logOut()
    } else {
    const { history } = this.props;
    history.push(key);
    }
  }

  renderProfileMenu = () => {
    const { userInfo } = this.props;
    if (userInfo) {
      const { name } = userInfo;
      return (
        <SubMenu
          title={
            <>
              <Avatar style={{ backgroundColor: '#7265e6', verticalAlign: 'middle' }} size="large">
                {name.charAt(0)}
              </Avatar>
            </>
          }
        >
          <Item disabled>
            {`Hi ${name}`}
          </Item>
          {profileMenuConfig.map(config => (
            <UiRestriction key={config.link} {...config}>
              <Item>{config.name}</Item>
            </UiRestriction>
          ))}
        </SubMenu>
      )
    }
    return <></>;
  }

  render() {
    const { children, location } = this.props;
    return (
      <Layout>
        <Header style={{ position: 'fixed', zIndex: 3, width: '100%' }}>
          <div className="logo" />
          <Menu
            theme="dark"
            mode="horizontal"
            defaultSelectedKeys={[location.pathname]}
            onClick={this.onMenuItemClick}
          >
            {layoutConfig.map(config =>
              <UiRestriction key={config.link} {...config}>
                {config.subMenu ? this[config.subMenu]() : <Item>{config.name}</Item>}
              </UiRestriction>
            )}
          </Menu>
        </Header>
        <Content className="site-layout" style={{ marginTop: 64 }}>
          <div className="site-layout-background" style={{ padding: 24 }}>
            {children}
          </div>
        </Content>
        <UiRestriction hidden >
          <InquiryForm />
        </UiRestriction>
        <Footer style={{ textAlign: 'center' }}>Ant Design Â©2018 Created by Ant UED</Footer>
      </Layout>
    )
  }
}

export default withRouter(UseContext(UserContext)(MainLayout));